@import 'bootstrap-normalize.css';
header {
  display: flex;
  justify-content: space-between;
  background-color: rgb(223, 217, 201);
  align-items: center;
  min-height: 50px;
  width: 100%;
  z-index: 10;
  padding: 0 20px;
  margin-bottom: 40px;
}
header .back_button {
  font-size: 34px;
  height: 36px;
  display: flex;
  visibility: hidden;
  justify-content: center;
  align-items: center;
  padding: 0 10px 6px 10px;
  margin-left: -10px;
  cursor: pointer;
  transition: 300ms;
}
header .back_button:hover {
  background-color: rgb(233, 230, 221);
}
header .login_wrapper {
  display: flex;
}
header .login_button {
  font-weight: 600;
  padding: 4px 10px;
  margin-right: -10px;
  cursor: pointer;
  transition: 300ms;
}
header .login_button:hover {
  background-color: rgb(233, 230, 221);
}
header .register_button {
  margin: auto 0;
  padding: 4px 10px;
  cursor: pointer;
  transition: 300ms;
}
header .register_button:hover {
  background-color: rgb(233, 230, 221);
}
header .forms_overlay {
  position: absolute;
  top: 50px;
  left: 0;
  height: calc(100vh - 50px);
  width: 100vw;
  z-index: 9;
  visibility: hidden;
  transition: 300ms;
}
header .login_form {
  position: absolute;
  display: flex;
  flex-direction: column;
  background-color: rgb(223, 217, 201);
  transition: 300ms;
  opacity: 0;
  padding: 10px 12px;
  visibility: hidden;
  top: 60px;
  right: 10px;
  z-index: 10;
  box-shadow: 0 0 40px rgba(0, 0, 0, 0.1);
}
header .login_form input {
  margin-bottom: 5px;
  padding-left: 8px;
  border: none;
  background-color: rgb(240, 240, 240);
}
header .login_form button {
  margin-top: 5px;
  height: 28px;
  background-color: rgb(233, 230, 221);
  transition: 300ms;
}
header .login_form button:hover {
  background-color: rgb(240, 240, 240);
}
header .login_form > div {
  font-size: 14px;
  cursor: pointer;
}
header .login_form > div:hover {
  text-decoration: underline;
}
header .register_form {
  position: absolute;
  display: flex;
  flex-direction: column;
  background-color: rgb(223, 217, 201);
  transition: 300ms;
  opacity: 0;
  width: 300px;
  padding: 10px 12px;
  visibility: hidden;
  top: 60px;
  right: 10px;
  z-index: 10;
  box-shadow: 0 0 40px rgba(0, 0, 0, 0.1);
}
header .register_form input {
  margin-bottom: 5px;
  padding-left: 8px;
  border: none;
  background-color: rgb(240, 240, 240);
}
header .register_form button {
  margin-top: 5px;
  height: 28px;
  background-color: rgb(233, 230, 221);
  transition: 300ms;
}
header .register_form button:hover {
  background-color: rgb(240, 240, 240);
}
header .pass_recovery_form {
  position: absolute;
  display: flex;
  flex-direction: column;
  background-color: rgb(223, 217, 201);
  max-width: 260px;
  transition: 300ms;
  opacity: 0;
  width: 300px;
  padding: 10px 12px;
  visibility: hidden;
  top: 60px;
  right: 10px;
  z-index: 10;
  box-shadow: 0 0 40px rgba(0, 0, 0, 0.1);
}
header .pass_recovery_form label {
  margin-bottom: 5px;
}
header .pass_recovery_form input {
  margin-bottom: 5px;
  padding-left: 8px;
  border: none;
  background-color: rgb(240, 240, 240);
}
header .pass_recovery_form button {
  margin-top: 5px;
  height: 28px;
  background-color: rgb(233, 230, 221);
  transition: 300ms;
}
header .pass_recovery_form button:hover {
  background-color: rgb(240, 240, 240);
}
header .login_info {
  display: none;
}
header .login_info:hover .player_stats {
  visibility: visible;
  opacity: 1;
  top: 60px;
  right: 40px;
}
header .nickname {
  cursor: pointer;
  padding: 4px 10px;
}
header .player_stats {
  position: fixed;
  display: flex;
  flex-direction: column;
  background-color: rgb(223, 217, 201);
  transition: 300ms opacity;
  visibility: hidden;
  opacity: 0;
  padding: 10px 12px 4px 12px;
  font-weight: 600;
  width: 200px;
  top: 60px;
  right: 40px;
  z-index: 10;
  box-shadow: 0 0 40px rgba(0, 0, 0, 0.1);
}
header .player_stats .player_stats_item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
}
header .logout {
  padding: 4px 10px;
  margin-right: -10px;
  cursor: pointer;
  transition: 300ms;
}
header .logout:hover {
  background-color: rgb(233, 230, 221);
}

.form_active {
  visibility: visible !important;
  opacity: 1 !important;
}

footer {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100px;
  font-size: 14px;
  margin-top: auto;
}

body {
  position: relative;
  display: flex;
  align-items: center;
  flex-direction: column;
  min-height: 100vh;
  background-color: rgb(233, 230, 221);
  overflow-x: hidden;
}

#loading {
  position: absolute;
  top: 0;
  left: 0;
  margin-bottom: 10px;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  backdrop-filter: blur(3px);
  z-index: 10;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 28px;
  color: #fff;
  letter-spacing: 3px;
}
#loading .left_cross {
  width: 70px;
  height: 4px;
  animation: left_cross_animation 1s infinite;
  transform-origin: 40% 10%;
  padding-bottom: 10px;
}

@keyframes left_cross_animation {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
#alert {
  position: absolute;
  top: -40px;
  background-color: rgb(204, 223, 201);
  padding: 5px 10px;
  transition: 400ms top;
  text-align: center;
  max-width: 250px;
  z-index: 999;
}

.tictac_wrapper {
  position: relative;
  display: flex;
  align-items: center;
  flex-direction: column;
  width: 100%;
  max-width: 302px;
}
.tictac_wrapper .tictac_title {
  margin-bottom: 20px;
  text-align: center;
}
.tictac_wrapper .game_info {
  display: flex;
  justify-content: space-between;
  align-items: baseline;
  width: 100%;
}
.tictac_wrapper .now_turn span {
  font-size: 20px;
  font-weight: bold;
  color: blue;
}
.tictac_wrapper #now_turn_tip {
  font-size: 14px;
  color: rgba(0, 0, 0, 0.5);
  font-weight: normal;
}
.tictac_wrapper .tictac_field {
  display: flex;
  flex-wrap: wrap;
  width: 302px;
  height: 302px;
  background-color: rgb(223, 217, 201);
  border: 1px solid black;
}
.tictac_wrapper .tictac_cell {
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid black;
  width: 100px;
  height: 100px;
  cursor: pointer;
}
.tictac_wrapper .result_popup {
  position: absolute;
  text-align: center;
  left: -100%;
  width: 270px;
  top: 60%;
  opacity: 0;
  transform: translate(-50%, -50%);
  transition: 500ms;
  backdrop-filter: blur(6px);
  padding: 25px 35px;
}
.tictac_wrapper .score {
  font-size: 20px;
  margin-top: 15px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.tictac_wrapper .score span {
  font-size: 24px;
  margin: 0 8px;
  font-weight: 600;
}
.tictac_wrapper .score span:nth-child(1) {
  color: blue;
}
.tictac_wrapper .score span:nth-child(2) {
  color: red;
}
.tictac_wrapper .win_message {
  margin-top: -5px;
}
.tictac_wrapper #newGame {
  width: 150px;
  height: 34px;
  background-color: rgba(255, 255, 255, 0.6);
  margin-top: 18px;
  transition: 300ms;
}
.tictac_wrapper #newGame:hover {
  background-color: rgba(255, 255, 255, 0.8);
}

.circle::before {
  content: "";
  width: 50px !important;
  height: 50px;
  border: 4px solid red;
  border-radius: 50%;
}

.cross::before, .cross::after {
  content: "";
  position: absolute;
  width: 56px;
  height: 4px;
  background-color: blue;
}

.cross::before {
  transform: rotate(-45deg);
}

.cross::after {
  transform: rotate(45deg);
}

.chat_wrapper {
  max-width: 302px;
}

.chat_window {
  background-color: rgb(223, 217, 201);
  margin-top: 20px;
  padding: 2px;
  border: 2px solid black;
  max-height: 200px;
  overflow-y: scroll;
  display: flex;
  flex-direction: column;
  scroll-behavior: smooth;
}
.chat_window::-webkit-scrollbar {
  width: 0px;
}

.chat_input {
  display: flex;
  align-items: center;
  margin-top: 10px;
}
.chat_input input {
  width: calc(100% - 106px);
  height: 30px;
  border: 1px solid black;
  padding: 0 10px;
  font-size: 16px;
  background-color: rgb(223, 217, 201);
}
.chat_input input:focus {
  outline: none;
}
.chat_input button {
  width: 100px;
  height: 30px;
  margin-left: 10px;
  border: 1px solid black;
  background-color: rgb(223, 217, 201);
  cursor: pointer;
}

.message {
  position: relative;
  margin: 4px;
  padding: 4px 6px;
  background-color: rgb(233, 230, 221);
}
.message .message_ip {
  font-size: 11px;
  color: gray;
}
.message .message_time {
  font-size: 11px;
  color: gray;
  position: absolute;
  right: 6px;
  top: 4px;
}
.message .message_text {
  line-height: 18px;
  overflow: hidden;
}